@media (max-width: 650px) {
  nav ul {
    letter-spacing: .2ch;
    font-size: calc(1.2vh + .4vw);
    margin: 0 .5rem;
    margin-top: .5rem;
    padding: .7rem !important;
    text-align: center;
  }
  nav ul li {
    display: block;
    padding: .5rem;
    border: white 1.5px solid;
    width: 100%;
    margin: .5rem;
  }
  nav ul li a {
    width: 100%;
    text-align: center;
    text-decoration: none;
    padding: 1rem;
  }
  #social-media-links a {
    font-size: 5vw !important;
  }
  nav ul li a:hover {
    border-bottom: white 1px solid !important;
  }
  #home-page-pattern-header {
    margin: 0;
    padding-top: 5vw;
    display: block;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    background-color: rgb(241, 240, 240);
    padding-bottom: 5%;
  }
  #home-page-pattern-header button {
    width: 80%;
    letter-spacing: .4ch;
    white-space: normal;
    text-decoration: none;
    padding: 1.5rem;
    font-size: calc(1.7vh + .5vw);
    border: #ffc 2.5px solid;
  }
  nav ul li a:hover {
    border-bottom: none;
  }
  footer {
    font-size: calc(1.5vh + .5vw);
  }
  footer p a i {
    font-size: calc(2vh + .5vw);
  }
}
